import React from "react";
import "./Card.css";
const Card = (props) => {
	return (
		<div className="container-fluid mx-auto justify-content-center text-center">
			<div className="row">
				<div className="card col-lg-6 col-md-8 col-sm-12 mx-auto">
					{props.children}
				</div>
			</div>
		</div>
	);
};
export default Card;



