import React, { Fragment } from "react";
import "./NavItem.css";
const NavItem = () => {
	const items = [
		{ href: "#home", link: "Home" },
		{ href: "#booking", link: "Booking" },
		{ href: "#about", link: "About" },
		{ href: "#contact", link: "Contact" },
	];

	const menuItem = items.map((item) => (
		<li className="nav-item">
			<a href={item.href} className="nav-link">
				{item.link} 
			</a>
		</li>
	));
	return <Fragment>{menuItem}</Fragment>;
};
export default NavItem;



