import React from "react";


const Input = (props) => {

	return (
		<div
			className={props.divClassName}
		>
			<label htmlFor={props.id}>{props.label}</label>
			<input
				className={props.className}
				type={props.type}
				id={props.id}
				{...props}
				
			/>
	
			<br />
		</div>
	)
}

export default Input;



