import React from "react";
import "./Button.css";
const Button = (props) => {
	return (
		<button
			type={props.type}
			className="btn btn-outline-secondary bg-white btn-lg mx-auto"
			id={props.id}
		>
			{props.label}
		</button>
	);
};
export default Button;



