import React from "react";
import Wrappy from "../components/Wrappy";
const AfterSignUp = () => {
	return (
		<Wrappy>
			<h1 className="title">Please check your email!</h1>
			<p>
				Dear adventurer, we sent you an authentication link to your email,
				please click on it to finish your registration and return here to plan
				your journey!
			</p>
		</Wrappy>
	);
};
export default AfterSignUp;



