import React from "react";
import Wrappy from "../components/Wrappy";
const SectionProfile = () => {
	return (
		<section>
		<Wrappy>
<h1 className="title">Here's your profile page.</h1>
<p>Here you'll be able to update your credentials and store them to book your trips easily :)</p>
		</Wrappy>
</section>
		);
};
export default SectionProfile;


