import React from "react";
import Wrappy from "../components/Wrappy";
import "./SectionContact.css";
const SectionContact = () => {
	return (
		<section id="contact">
			<Wrappy>
				<h1 className="title">Contact</h1>
				<div id="contact_part">
					<p id="contact">
						If you have any questions, wishes or messages you'd like to send us,
						please use our email sibitravel@protonmail.com.
					</p>
					<p id="contact">Phone Number: +7 683 355 825</p>
					<p id="contact">
						Company adress: <br />
						SibiTravel Co. <br />
						Proletarskaya 63 <br />
						372 91 <br />
						Moscow
					</p>
				</div>
			</Wrappy>
		</section>
	);
};

export default SectionContact;



