import React, { useContext } from "react";
import { Route } from 'react-router-dom';
import SectionHome from "./sections/SectionHome";
import SectionBooking from "./sections/SectionBooking";
import SectionAbout from "./sections/SectionAbout";
import SectionLogin from "./sections/SectionLogin";
import SectionContact from "./sections/SectionContact";
import Welcome from "./components/Welcome";
import AfterSignUp from "./sections/AfterSignUp";
import AuthContext from "./components/auth-context";
import Layout from "./components/Layout";
import SectionProfile from "./sections/SectionProfile";

function App() {
	const authCtx = useContext(AuthContext);
	return (
		<div>
			<Layout>
				<Route path='/welcomepage'>
				{authCtx.isLoggedIn && <Welcome />}
				</Route>
				<Route path='/signedup'>
				<AfterSignUp />
				</Route>
				{authCtx.isLoggedIn &&
				<Route path='/profile'>
				<SectionProfile />
				</Route>}
				<Route path='/login'>
					{!authCtx.isLoggedIn && <SectionLogin />}
					</Route>
			<SectionHome />
			{!authCtx.isLoggedIn && <SectionBooking />}
			<SectionAbout />
					
			
			<SectionContact />
			</Layout>

		</div>
	);
}
export default App;



