import React from "react";
import sibirWinter from "../img/sibir_winter.jpg";
import "./SectionHome.css";
import Wrappy from "../components/Wrappy";
const SectionHome = () => {
	return (
		<section id="home">
			<Wrappy>
				<h1 className="title">Welcome to SibiTravel!</h1>
				<p className="info">
					Do you feel frustrated with your job? Do you miss adventure and
					mystery in your life? Do you feel like every day is the same? You're
					at the right place. You can get away from everyday stress generated
					from work and covid pandemic. Leave it all behind you for a while and
					travel to the distant picturesque lanscape of Siberia! Book your
					holiday with us and discover the magical beauty of siberian forests,
					mountains and lakes, far in a place where a piece of untouched
					wilderness remained despite human activity. You can choose from a
					variety of 20 destinations all around Siberia. We offer trips that
					last from 2 days to 2 weeks. Among the most amazing places that you shouldn't miss during your trip are: Ergaki National Park, Altai Mountains, Lake Baikal, horrendous gulags from Stalin era, legendary Trans-Siberian Railway and the city of Tomsk. If your lucky enough, maybe you'll get to see the silver tiger as well!
				</p>
			</Wrappy>
			<div className="container-fluid justify-content-center">
				<img
					src={sibirWinter}
					className="img-fluid img_gallery mx-auto"
					id="pic-home"
					alt="Siberia-in-winter"
				/>
			</div>
			<hr />
		</section>
	);
};
export default SectionHome;



