import "./Header.css";
import React from "react";
import headerImage_sm from "../img/northernlights_sm.jpg";
import headerImage_md from "../img/northernlights_md.jpg";
import headerImage_lg from "../img/northernlights_lg.jpg";

const Header = () => {
	return (
		<div className="container-fluid text-right">
			

<picture>
  <source media="(min-width: 480px)"
  srcset={`${headerImage_sm} 400w,
  ${headerImage_md} 1000w,
  ${headerImage_lg} 1400w`}
  sizes="100vw"/>
  <img src={headerImage_sm} className="img-fluid" id="header-img" alt="Northern lights over dark mountains"/>
  </picture>
			<h1 className="heading">Ready for a new adventure?</h1>
			<br />
			<h2 className="sub-heading">Book your best trip to Siberia with us!</h2>
		</div>
	);
};
export default Header;



