import React from "react";
import "./Footer.css";
const Footer = () => {
	return (
		<footer className="page-footer">
			<div className="container-fluid text-center">
				<div className="row">
					<p className="py-3 align-middle">
						<i class="far fa-copyright"></i> SibiTravel 2021
					</p>
				</div>
			</div>
		</footer>
	);
};
export default Footer;



