import React, {
	useState,
	useEffect,
	useContext
} from "react";
import Card from "../components/Card";
import Button from "../components/UI/Button";
import AuthContext from "../components/auth-context";
import Input from "../components/Input";
import {  useHistory } from 'react-router-dom';


const SectionLogin = () => {
	const history = useHistory();
	const [enteredEmail, setEnteredEmail] = useState('');
  	const [enteredPassword, setEnteredPassword] = useState('');
  	const [emailIsValid, setEmailIsValid] = useState('');
	const [passwordIsValid, setPasswordIsValid] = useState('');
	const [formIsValid, setFormIsValid] = useState(false);
	const [loading, setLoading] = useState(false);
	const [isLogin, setIsLogin] = useState(true);

	
	const authCtx = useContext(AuthContext);

	const switchAuthModeHandler = () => {
			setIsLogin((prevState) => !prevState);
		};
		
	useEffect(() => {
		
		const identifier = setTimeout(() => {
		  setFormIsValid(
			enteredEmail.includes('@') && enteredPassword.trim().length > 7 
		  );
		}, 500);
	
		return () => {
		  clearTimeout(identifier);
		};
	  }, [enteredEmail, enteredPassword]);
	
	  const emailChangeHandler = (event) => {
		setEnteredEmail(event.target.value);
	};
	const passwordChangeHandler = (event) => {
			setEnteredPassword(event.target.value);
	};
	  
	  const validateEmailHandler = () => {
		setEmailIsValid(enteredEmail.includes('@') && enteredEmail.trim().length > 6);
	  };
	
	  const validatePasswordHandler = () => {
		setPasswordIsValid(enteredPassword.trim().length > 7);
	  };
	
	
	const submitHandler = (event) => {
		event.preventDefault();
	

		setLoading(true);
		let url;
		if (formIsValid === false) {
			return false
		}
		else if	(isLogin) {
			url =
				"https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyBrAkP16xH0R1tBM7gyOYbYDFUcJpkzCdQ";
		} else {
			url =
				"https://identitytoolkit.googleapis.com/v1/accounts:signUp?key= AIzaSyBrAkP16xH0R1tBM7gyOYbYDFUcJpkzCdQ";
			}
			fetch(url, {
				method: "POST",
				body: JSON.stringify({
					email: enteredEmail,
					password: enteredPassword,
					returnSecureToken: true,
				}),
				headers: { "Content-Type": "application/json" },
			})
		      .then((res) => {
        setLoading(false);
        if (res.ok) {
          return res.json();
        } else {
          return res.json().then((data) => {
            let errorMessage = 'Invalid input!';

            throw new Error(errorMessage);
          });
        }
      })
      .then((data) => {

        const expirationTime = new Date(
          new Date().getTime() + +data.expiresIn * 1000
        );
        authCtx.login(data.idToken, expirationTime.toISOString());
        isLogin === false ? history.push('/signedup') : history.push('/welcomepage')
      })
      .catch((err) => {
        alert(err.message);
      });
  };
	  

	return (
		<section id="login">
			<Card>
				<h1 className="title">{isLogin ? "Login" : "Sign Up"}</h1>				
				{isLogin === true ? <p>
				 Don't have an account yet?
					<a className="card_link" onClick={switchAuthModeHandler}>
						Sign Up
					</a>
				</p> : <p>
		I already have an account.
		   <a className="card_link" onClick={switchAuthModeHandler}>
			   Login
		   </a>
	   </p>}
				<form 
					id="loginform"
					onSubmit={submitHandler}
				>
				
					<Input
					label="Email:"
					divClassName="form-group"
						className={`form-control ${emailIsValid === false ? "is-invalid" : ""}`}
						id="user_email_input"
						name="user_email_input"
						type="email"
						placeholder="Your email"
						value={enteredEmail}
						onChange={emailChangeHandler}
						onBlur={validateEmailHandler}
					/>
					
					
					<Input
					label="Password:"
					divClassName="form-group"
						className={`form-control ${passwordIsValid  === false ? "is-invalid" : ""}`}
						id="user_password_input"
						name="user_password_input"
						type="password"
						placeholder="Your password"
						value={enteredPassword}
						onBlur={validatePasswordHandler}
						onChange={passwordChangeHandler}
						
					/>

					<div class="form-check">
							<input
								type="checkbox"
								className="form-check-input"
								value="question"
								id="lastquestion"
							required={!isLogin}
							/><label className="form-check-label" htmlFor="lastquestion">{isLogin ? "Stay logged in." : "I agree to the company's privacy policy."}
							</label>
								
							<div className="invalid-feedback">
      You have to agree in order to sign up.
    				</div>
							
					
					</div>
					<Button id="submit_button" type="submit" disabled={loading} label={isLogin ? "Login" : "Create Account"}></Button>

				</form>
			</Card>
			<hr />
		</section>
	);
	};

export default SectionLogin;



