import React from "react";
import sibir_summer from "../img/sibir_summer.jpg";
import sibir_husky from "../img/SIBERIAN-HUSKY.jpg";
import lake_Baikal from "../img/Lake Baikal.jpg";
import white_tiger from "../img/White-Siberian-Tiger.jpg";
import video_poster from "../img/sibipic-lg.jpg";
import Wrappy from "../components/Wrappy";
import SibiVideo from "../img/sibitravelvideo.mp4";
import "./SectionAbout.css";
const SectionAbout = (props) => {
	return (
		<section id="about">
			<Wrappy>
				<h1 className="title">About SibiTravel</h1>
				<p className="info">
					We're the #1 travel company that organises trips to Siberia! Book your
					trip with us and you surely won't be disappointed! Our company was
					established in 1997 and has grown ever since. Every year we have about
					5000 satisfied customers from 30 countries around the world. Moreover,
					we organise tourist trips to more than 20 incredibly beautiful
					destinations all around Siberia with professional local guides. Don't hesitate and book your trip to Siberia today! You won't regret it!
				</p>
			</Wrappy>
			<div className="container-fluid text-center">
			<div className="embed-responsive embed-responsive-4by3">
		
					<video
						className="embed-responsive-item  mx-auto"
						id="sibi_video"
						src={SibiVideo}
						poster={video_poster}
						controls
						autoPlay
					>
						Your browser does not support HTML5 video. Get the
						<a href="highlight_reel.mp4">MP4 video</a>
					</video>
					<p>Here's a better quality video which didn't fit my free GitHub account: <a href="https://www.youtube.com/watch?v=_CWLDbm7IWs">Video</a></p>
				</div> 
			</div>
			<hr />
			<div className="container-fluid text-center" id="gallery">
				<div className="row ">
					<h1 className="title">Gallery</h1>
				</div>

				<img
					src={sibir_summer}
					id="pic-about"
					alt="Siberian forest in summer"
					className="img-fluid d-block mx-auto img_gallery"
				/>
				<br />
				<p>Siberian forest in summer</p>

				<img
					src={sibir_husky}
					id="pic-about"
					alt="Three Siberian huskies"
					className="img-fluid d-block mx-auto img_gallery"
				/>
				<br />
				<p>Happiness is running in snow with your friends</p>

				<img
					src={lake_Baikal}
					id="pic-about"
					alt="Lake Baikal"
					className="img-fluid d-block mx-auto img_gallery"
				/>
				<br />
				<p>Lake Baikal</p>
				<img
					src={white_tiger}
					id="pic-about"
					alt="White Siberian Tiger"
					className="img-fluid  d-block mx-auto img_gallery"
				/>
				<br />
				<p>Unique White Siberian Tiger</p>
			</div>

			<hr />
		</section>
	);
};
export default SectionAbout;



