import React, { useState } from "react";
import Card from "../components/Card";
import Button from "../components/UI/Button";
import Input from "../components/Input";


const SectionBooking = (props) => {
	const [fullName, setFullName] = useState("")
	const [emailBooking, setEmailBooking] = useState("")
	const [timeFrom, setTimeFrom] = useState("")
	const [timeUntil, setTimeUntil] = useState("")
	const [whereTo, setWhereTo] = useState("")
	const [numberOfTravellers, setNumberOfTravellers] = useState("")


	const fullNameHandler = (event) => {
		setFullName(event.target.value);
}
const emailBookingHandler = (event) => {
	setEmailBooking(event.target.value);
}
const timeFromHandler = (event) => {
	setTimeFrom(event.target.value);
}
const timeUntilHandler = (event) => {
	setTimeUntil(event.target.value);
}
const whereToHandler = (event) => {
	setWhereTo(event.target.value);
}
const numberOfTravellersHandler = (event) => {
	setNumberOfTravellers(event.target.value);
}


	var number_travellers = [];
	for (let i = 0; i < 11; i++) {
		number_travellers.push(<option value={i}>{i}</option>);
	}
	const submitBookingHandler = (event) => {
event.preventDefault();

	}
	return (
		<section id="booking">
			<div className="container text-center">
				<h1 className="title">
					Here's our booking form to plan your magnificent trip to the North!
				</h1>
			</div>
			<Card>
				<form id="form" onSubmit={submitBookingHandler} action="https://www.freecodecamp.com/email-submit)">

						<Input
							divClassName="form-group"
						label="Full Name:"
							id="fullname"
							className="form-control"
							type="text"
							name="fullname"
							required
							value={fullName}
							onChange={fullNameHandler}
						/>
					
						<Input
						divClassName="form-group"
							id="useremail"
							type="email"
							className="form-control"
							name="useremail"
							required
							label="Email:"
							value={emailBooking}
							onChange={emailBookingHandler}
						/>
						<Input
						label="From:"
						divClassName="form-group"
							id="date_from"
							name="input_from"
							className="form-control"
							type="date"
							required
							value={timeFrom}
							onChange={timeFromHandler}
						/>
				
						<Input
						label="Until:"
						divClassName="form-group"
							id="date_until"
							name="input_until"
							className="form-control"
							type="date"
							required
							value={timeUntil}
							onChange={timeUntilHandler}
						/>
						<Input
						divClassName="form-group"
						label="Where to:"
							id="where_to"
							name="input_whereto"
							className="form-control"
							type="text"
							required
							value={whereTo}
							onChange={whereToHandler}
						/>
					<div className="form-group">
						<label htmlFor="number_travellers">Number of travellers:</label>
						<select
							id="number_travellers"
							name="input_number"
							className="form-select"
							required
							value={numberOfTravellers}
							onChange={numberOfTravellersHandler}
						>
							{number_travellers}
						</select>
					</div>

					<Button type="submit" id="submit_button" label="Submit" />
				</form>
			</Card>
			<hr />
		</section>
	);
};
export default SectionBooking;



