import React, { useContext } from "react";
import AuthContext from "./auth-context";
import NavItem from "./NavItem";
import { Link } from 'react-router-dom';

import "./Navbar.css";
const Navbar = () => {
	const ctx = useContext(AuthContext);
	const logoutHandler = () =>{
		ctx.logout()
	}
	return (
		<nav className="navbar navbar-expand-md navbar-dark">
			<div className="container-fluid">
				<div className="navbar-header">
					<a className="navbar-brand" href="#home">
						Sibitravel AB
					</a>
				</div>
				<ul className="nav navbar-nav">
					<NavItem />
					{!ctx.isLoggedIn && <li id="added-nav-item" className="nav-item">
					<Link to="/login" className="nav-link">
				Login
			</Link>
		</li>}

		{ctx.isLoggedIn && <li id="added-nav-item" className="nav-item">
			<Link to="/profile" className="nav-link">
				Profile
			</Link>
		</li>}
		{ctx.isLoggedIn &&
						<li className="nav-item nav-link" id="added-nav-item" onClick={logoutHandler}>Logout</li>}
				</ul>
				
					<ul id="navbar_icons" className="nav navbar-nav navbar-right">
						{!ctx.isLoggedIn && <li>
							<Link to="/login">
								<i className="fas fa-sign-in-alt"></i>
							</Link>
						</li>}
						{ctx.isLoggedIn &&
						<li>
							<Link to="/profile">
								<i className="fas fa-user"></i>
							</Link>
						</li>}
						{ctx.isLoggedIn &&
						<li><button className="btn btn-sm btn-dark nav-link " onClick={logoutHandler}>Logout</button></li>}
					</ul>
				
				
			</div>
		</nav>
	);
};
export default Navbar;



