import React from "react";

const Wrappy = (props) => {
	return (
		<div className="container text center justify-content-center">
			<div className="row">
				<div className="col-sm-12 col-md-9 col-lg-7 mx-auto my-5">
					{props.children}
				</div>
			</div>
		</div>
	);
};
export default Wrappy;



