import React from 'react';
import Header from "./Header";
import Navbar from "./Navbar";
import Footer from "./Footer";

const Layout = (props) => {
    return (
        <div>
        <Header />
			<Navbar />
            {props.children}
            <Footer />
</div>
    )
};
export default Layout;


